html {
    height: 100%;
}

body {    
    margin: 0;
    padding: 0;
    
    height: 100%;

    background-color: rgb(30, 30, 30);
}

.home-root {
    display: grid;
    grid-template-columns: auto 40%;

    height: 100%;

    h4 {
        display: flex;
        flex-shrink: 0;
        flex-direction: column;
        justify-content: center;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        margin: 0;
        padding: 0;

        height: 30px;

        padding-left: 20px;
        border-style: dashed;
        border-width: 2px;

        color: lightgray;
        background-color: rgb(51,51,51);
    }

    .canvas {
        display: flex;
        flex: 1 1 auto;

        width: 100%;
        height: 100%;

        touch-action: none;
    }

    .editor {
        display: flex;
        flex-direction: column;
        align-content: stretch;

        flex: 1 1 auto;
        overflow: auto;

        color: lightgray;

        .code {
            display: flex;
            flex: 1 1 auto;

            flex-direction: row;      
            
            .tree-panel {
                display: flex;
                flex-direction: column;
                align-items: stretch;

                border-style: none dashed none none;
                border-width: 2px;
                
                .tree {
                    display: flex;
                    flex-grow: 1;

                    overflow: hidden;

                    min-width: 200px;
                    max-width: 300px;

                    background-color: rgb(37, 37, 38);
                }
            }
        }
    }
}